import { Component, OnInit, Inject } from '@angular/core';

import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

@Component({
  selector: 'app-passport-dialog',
  templateUrl: './passport-dialog.component.html',
  styleUrls: ['./passport-dialog.component.scss']
})
export class PassportDialogComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<PassportDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Object
  ) { }

  ngOnInit() {
    console.log('data: ', this.data);
  }

  cancel(): void {
    this.dialogRef.close();
  }
}
