import { Component, OnInit } from '@angular/core';

import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';

import {PassportDialogComponent} from '../passport-dialog/passport-dialog.component';
import {AddressDialogComponent} from '../address-dialog/address-dialog.component';

@Component({
  selector: 'app-offer-application',
  templateUrl: './offer-application.component.html',
  styleUrls: ['./offer-application.component.scss']
})
export class OfferApplicationComponent implements OnInit {

  constructor(public dialog: MatDialog) { }

  ngOnInit() {}

  openPassportDialog(): void {
    const dialog = this.dialog.open(PassportDialogComponent, {
      width: '650px',
      data: {}
    });

    dialog.afterClosed().subscribe(result => {
      console.log('result: ', result);
    })
  }

  openAddressDialog(title: String): void {
    const dialog = this.dialog.open(AddressDialogComponent, {
      width: '450px',
      data: {
        title: title,
        address: {},
      }
    });

    dialog.afterClosed().subscribe(result => {
      console.log('result: ', result);
    })
  }
}
